/**
 * Dynamic route with server data fetching.
 */

import type { Context } from "oxian-js/types.ts";

export async function getServerSideProps(ctx: Context) {
  const { id } = ctx.request.pathParams;
  const item = { id, title: `Item ${id}`, time: Date.now() };
  return { item };
}

export default function ItemPage(props: { item: { id: string; title: string; time: number } }) {
  return (
    <main style={{ fontFamily: "sans-serif", padding: 24 }}>
      <h1>{props.item.title}</h1>
      <p>ID: {props.item.id}</p>
      <p>Server time: {new Date(props.item.time).toISOString()}</p>
    </main>
  );
}


